import React, {useEffect, useState} from "react";

let res = "lmao";
const Upload = (props) => {
    const [Picture, setPicture] = useState('');
    const [Submit, setSubmit] = useState(false);

    useEffect(() => {
        const requestOptions = {
            method: 'POST',
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                url: Picture
            })
        };
        if (Submit) {
            setSubmit(false);
            fetch("/upload", requestOptions)
                .then(response => response.json())
                .then(data => {
                    console.log("Picture uploaded!");
                    console.log(data);
                    res =  (JSON.stringify(data));
                    setPicture("");

                })
        }
    }, [Submit, Picture ,props]);

    let handlePicture = (e) => {
        const picURL = URL.createObjectURL(e.target.files[0]);
        setPicture(picURL);
    };
    let handleSubmit = (e) => {
        e.preventDefault();
        setSubmit(true);
    };

    return (
        <div className="register_wrapper">
                    <form className="myForm">
                            <h2 className="formTitle" >Sign Up {res}</h2>
                                <input id="profilePic" type="file" onChange={handlePicture}/>
                                <img className="playerProfilePic_home_tile"  src={Picture}/>
                        <input type="submit" name="submit" value="See Venue Rankings" onSubmit={handleSubmit}/>
                    </form>
        </div>
    );
};

export default Upload;